@import url('https://fonts.googleapis.com/css2?family=Inter:wght@800&display=swap');
* {
    font-family: 'Inter';
    padding: 0;
}

#police-hub-container {
    background-color: #211F1F;
    width: 350px;
    border-radius: 7px;
    position: relative;
    user-select: none;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    min-height: 169px;
}

.hub-container {
    display: flex;
}

.police-hub {
    color: white;
    position: relative;
    border-top-left-radius: 7px;
    padding-top: 1px;
    padding-left: 10px;
    width: 54%;
    height: 20%;
    background-color: #292929;
    font-size: 6px;
    margin-right: 5px;
    margin-bottom: 10px;
}

.officer,
.officers-container {
    font-size: 7px;
}

.officer {
    padding: 0 13px 0 13px;
}

.officer-name {
    color: white;
    margin-right: auto;
}

.officers-container {
    display: flex;
    flex-direction: column;
}

.officer {
    display: flex;
    background-color: #2B2B2B;
    align-items: center;
    margin-bottom: 7px;
}

.green {
    width: 13px;
    height: 14px;
    background-color: #3FD13C;
    border-radius: 4px;
}

.out {
    width: 13px;
    height: 14px;
    background-color: #CC1C11;
    border-radius: 4px;
}

/* .busy {
    width: 13px;
    height: 14px;
    background-color: #CCA311;
    border-radius: 4px;
} */

.officer-callsign {
    color: #FFFFFF;
    opacity: 70%;
    margin-right: 2px;
}

.purple-line {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 0.5px;
    width: 100%;
    background-color: #6A4BC5;
}

.hub-buttons {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.officer-rank {
    color: #FFFFFF;
    margin-right: 4px;
    opacity: 70%;
}

.hub-button {
    border: 2px solid black;
    border-color: #343434;
    padding: 5px 8px;
    font-size: 7px;
    cursor: pointer;
    margin-right: 10px;
}

.app {
    position: absolute;
}

.hub-button i {
    font-size: 17px;
}

.hub-button.toggle {
    background-color: #6A4BC5;
}

.green,
.out,
.busy,
.officer-callsign {
    margin-right: 7px;
}

.hub-button.toggle i {
    color: white;
    opacity: 100%;
}

.hub-button h1,
.hub-button i {
    color: #FFFFFF;
    opacity: 70%;
}

.hub-button h1 {
    margin: 0;
}

.officer-radio-freq {
    background-color: #365EC2;
    color: white;
    width: 30px;
    font-size: 5px;
    height: 20px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* flagged menu , duty options */
#flag-duty-options {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

#flag-duty-options button {
    width: 330px;
    height: 17px;
    color: white;
    background-color: #6A4BC5;
    border: none;
    border-radius: 2px;
    font-size: 9px;
}

#flagged-menu {
    display: flex;
    justify-content: space-between;
    padding: 0 10px 0 10px;
}

#flagged-menu button {
    color: #FFFFFF;
    background-color: #393939;
    border: none;
    width: 85px;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
    height: 17px;
    font-size: 6px;
}

#flagged-menu button.clicked {
    border-bottom-style: solid;
    border-bottom-color: #6A4BC5;
}

.plate-input-container button {
    margin-left: 80px;
    color: white;
    background-color: #5F28A6;
    border: none;
    width: 80px;
    font-size: 6px;
    border-radius: 5px;
    height: 15px;
}

.callsign-input-container button {
    margin-left: 80px;
    color: white;
    background-color: #5F28A6;
    border: none;
    width: 80px;
    font-size: 6px;
    border-radius: 5px;
    height: 15px;
}

.plate-input {
    width: 110px;
    border-radius: 5px;
    margin-right: 20px;
    text-align: center;
    height: 47px;
    background-color: #D9D9D9;
}

.plate-input-container {
    display: flex;
    text-align: center;
    align-items: center;
    margin-top: 10px;
    padding: 0 40px 0 40px;
}

.callsign-input-container {
    display: flex;
