}

#callsign-input-text {
    font-size: 30px;
    background: none;
    border: none;
    color: white;
    outline: none;
    width: 260px;
    text-align: center;
}

#flag-plate-text {
    width: 110px;
    height: 47px;
    font-size: 9px;
    border: none;
    border-radius: 2px;
    outline: none;
    background-color: #343434;
}

textarea {
    color: white;
    font-size: 5px;
}

#send-btn {
    font-size: 7px;
    color: white;
    width: 44px;
    border: none;
