}

.flex-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.current-status {
    width: 6px;
    height: 6px;
    border-radius: 1px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

#duty-menu {
    display: flex;
    justify-content: space-between;
